import React from "react";
import "./navigation.css";

type NavigationProps = {
    handleClick: (e: React.MouseEvent<HTMLButtonElement>) => void;
    page: number;
};

export const Navigation = ({ handleClick, page }: NavigationProps) => (
    <nav>
        <ul>
            <li>
                <button
                    name="0"
                    onClick={handleClick}
                    className={page === 0 ? "is-dark" : ""}
                >
                    <span className="nav-dot"></span>
                    <span className="nav-label">Intro</span>
                </button>
            </li>
            <li>
                <button
                    name="1"
                    onClick={handleClick}
                    className={page === 1 ? "is-dark" : ""}
                >
                    <span className="nav-dot"></span>
                    <span className="nav-label">About</span>
                </button>
            </li>
            <li>
                <button
                    name="2"
                    onClick={handleClick}
                    className={page === 2 ? "is-dark" : ""}
                >
                    <span className="nav-dot"></span>
                    <span className="nav-label">Сontacts</span>
                </button>
            </li>
        </ul>
    </nav>
);
