import { getMonth, getYear } from "date-fns";

const startDate = new Date(2019, 6, 17);
const monthDiff = (dateFrom: Date, dateTo: Date): number =>
    getMonth(dateTo) -
    getMonth(dateFrom) +
    12 * (getYear(dateTo) - getYear(dateFrom));

const monthsNow = monthDiff(startDate, new Date());
const months = monthsNow % 12;
const years = Math.floor(monthsNow / 12);

export const getDate = (): string => {
    let date = "";
    if (years > 1) {
        date += years + " years ";
    }
    if (years === 1) {
        date += years + " year ";
    }
    if (months > 1) {
        date += months + " months ";
    }
    if (months === 1) {
        date += months + " month ";
    }
    return date;
};
