import React from "react";
import "./contacts.css";
import { ReactComponent as Google } from "assest/icons/google.svg";
import { ReactComponent as Github } from "assest/icons/github.svg";
import { ReactComponent as LinkedIn } from "assest/icons/linkedin.svg";
import { ReactComponent as Telegram } from "assest/icons/telegram.svg";

export const Contacts = () => (
    <div className="section" id="contacts">
        <div className="contacts">
            <div className="contacts__content">
                <div className="contacts__col">
                    <a
                        href="https://www.linkedin.com/in/morozserge/"
                        className="contacts__link"
                    >
                        <LinkedIn className="contacts__icon" />
                        <span className="contacts__text">/morozserge</span>
                    </a>
                    <a
                        href="https://t.me/morozserge"
                        className="contacts__link"
                    >
                        <Telegram className="contacts__icon" />
                        <span className="contacts__text">/morozserge</span>
                    </a>
                </div>
                <div className="contacts__col">
                    <a
                        href="https://github.com/morozserge1st"
                        className="contacts__link"
                    >
                        <Github className="contacts__icon" />
                        <span className="contacts__text">/morozserge1st</span>
                    </a>
                    <a
                        href="mailto:morozserge1st@gmail.com"
                        className="contacts__link"
                    >
                        <Google className="contacts__icon" />
                        <span className="contacts__text">
                            morozserge1st@gmail.com
                        </span>
                    </a>
                </div>
            </div>
        </div>
    </div>
);
