import React from "react";

type SectionProps = {
    date: string;
    company: string;
    duties: string[];
    technologies: string[];
    projects: string[];
};

export const Section = ({
    date,
    company,
    duties,
    technologies,
    projects,
}: SectionProps) => (
    <div className="about__experience">
        <small className="about__date">{date}</small>
        <div className="about__content">
            <h3 className="about__company">{company}</h3>
            <ul className="about__list">
                {duties.map((x) => (
                    <li key={x}>{x}.</li>
                ))}
            </ul>
            <h6 className="about__section">Technologies:</h6>
            <p className="about__list">
                {technologies.map((x, i) => (
                    <span key={i}>
                        {x}
                        {i === technologies.length - 1 ? "." : ", "}
                    </span>
                ))}
            </p>
            <h6 className="about__section">Projects:</h6>
            <ul className="about__list">
                {projects.map((x) => (
                    <li key={x}>{x}.</li>
                ))}
            </ul>
        </div>
    </div>
);
